@import "~semantic-ui-css/semantic.min.css";
@import "./components/manifest";
@import "./containers/manifest";

.no-margin {
  margin: 0 !important;
}

.section {
  padding: 8.871rem 0 !important;
}

.half-section {
  padding: 4rem 0 !important;
}

.quarter-section {
  padding: 1.5rem 0 !important;
}

.white-text {
  color: #fff !important;
}

.centered {
  margin: auto;
}

.margin-auto {
  margin-left: auto;
  margin-right: auto;
}

.justify-center {
  display: flex;
  justify-content: center;
}

.text-center {
  text-align: center;
}

.display {
  opacity: 1;
  transition: 0.5s !important;
}

.no-display {
  opacity: 0 !important;
  transition: 0.5s !important;
}

.display-inline-block {
  display: inline-block !important;
}

.borderless {
  box-shadow: 0 0 0 0px !important;
}

.tk-light-blue {
  background-color: #3a9dd9 !important;
  color: #fff !important;
}

.tk-blue {
  background-color: #064eab !important;
  color: #fff !important;
}

.tk-dark-blue {
  background-color: #022b6b !important;
  color: #fff !important;
}

.wider-line-height {
  h1,
  h2,
  h3,
  h4,
  h5,
  h6,
  p {
    line-height: 1.9em;
  }
}
