.demo-form {
  padding: 0 2rem !important;
}

.margin-bottom-small {
  margin-bottom: 1rem;
}

.demo-form-left {
  padding-top: 4.5rem;
  padding-bottom: 4.5rem;
  padding-left: 10% !important;
  padding-right: 10% !important;
}

@media (max-width: 768px) {
  .demo-form-left {
    padding-top: 6rem;
    padding-bottom: 6rem;
  }
}

@media (max-width: 472px) {
  .demo-form-left {
    padding-top: inherit;
    padding-bottom: 2.8rem;
  }
}
