.intro {
  height: 60vh;
  background-size: cover;
  background-position: center;
  background-blend-mode: overlay;

  img {
    max-width: 100%;
    max-height: 60vh;
  }
}

.overlay-text {
  position: absolute;
  margin: 0 auto;
  left: 0;
  right: 0;
  z-index: 1;
}

.white-background {
  background-color: #fff;
  color: #222 !important;
}

.light-gray-background {
  background-color: #f0f0f0;
}

.dark-background {
  background-color: #022b6a;
  color: #fff;
}

.darker-background {
  background-color: #0b1b2c;
  color: #fff;
}

.dark-gray-background {
  background-color: #222;
  color: #fff;
}

.black-background {
  background-color: #111;
  color: #fff;
}

.price {
  font-size: 3rem;
  margin-bottom: 1.2rem;
}

.sticky {
  position: sticky;
}

.fixed {
  position: fixed;
}

.top {
  top: 0;
}

.back {
  z-index: -1;
}

.front {
  z-index: 7;
}

.parallelogram-right {
  width: 240vw;
  height: 240vh;
  transform: skew(-45deg);
  background: #064eab;
}

.background-rectangle {
  width: 100vw;
  height: 100vh;
  z-index: -3;
}

#intro-banner {
  top: 35%;
  left: 40%;
  right: 5%;
  width: 50vw;
  text-align: right;

  h1 {
    color: #fff;
    font-size: 2em;
  }

  h3 {
    color: #fff;
    font-size: 1em;
    font-weight: 300;
  }
}

#section-three {
  height: 100vh;
  background-size: cover;
  background-position: center;
  background-blend-mode: overlay;
  display: flex;
  align-items: center;

  img {
    max-width: 100%;
    max-height: 100vh;
  }
}

#mobile-app {
  background-size: cover;
  background-position: center;
  background-blend-mode: overlay;
  height: 80vh;
  display: flex;
  align-items: center;

  img {
    max-width: 100%;
    max-height: 60vh;
  }
}

#triangle-topleft {
  width: 0;
  height: 0;
  border-top: 240vh solid #064eab;
  border-right: 100vw solid transparent;
}

#triangle-bottomright {
  width: 0;
  height: 0;
  border-bottom: 240vh solid #064eab;
  border-left: 100vw solid transparent;
}

@media (max-width: 768px) {
  h1 {
    font-size: 1.6em !important;
  }

  #intro-banner {
    top: 28% !important;
    left: 0 !important;
  }
}

@media (max-width: 472px) {
  #intro-banner {
    text-align: center !important;
  }
}
